body {
  overflow-x: hidden;
  font-family:  'Open Sans', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Catamaran';
  font-weight: 800 !important;
}
a{
  cursor:pointer;
}
.btn-xl {
  text-transform: uppercase;
  padding: 1.5rem 3rem;
  font-size: 0.9rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
}

.bg-black {
  background-color: #000 !important;
}

.rounded-pill {
  border-radius: 5rem;
}

.navbar-custom {
  /* padding-top: 1rem;
  padding-bottom: 1rem; */
  background-color: rgba(0, 0, 0, 0.7);
  transition: top 0.4s; /* Transition effect when sliding down (and up) */
}

.navbar-custom .navbar-brand {
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  font-weight: 700;
}

.navbar-custom .navbar-nav .nav-item .nav-link {
  text-transform: uppercase;
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
}

header.masthead {
  width: 100%;
  height: 685px;
  overflow: hidden;
  padding-top: calc(7rem + 72px);
  padding-bottom: 7rem;
  /* background: -webkit-gradient(linear, left bottom, left top, from(#ff6a00), to(#ee0979)); */
  /* background: linear-gradient(0deg, #ff6a00 0%, #ee0979 100%); */
  background-color: #5f5f5f;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: scroll;
  background-size: cover;
  background-position: top;
  z-index: -1;
}
#myCarousel .slide {
  width: 100%;
  height: 685px;
  overflow: hidden;
  padding-top: calc(7rem + 72px);
  padding-bottom: 7rem;
  /* background: -webkit-gradient(linear, left bottom, left top, from(#ff6a00), to(#ee0979)); */
  /* background: linear-gradient(0deg, #ff6a00 0%, #ee0979 100%); */
  background-color: #5f5f5f;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: scroll;
  background-size: cover;
  background-position: top;
  z-index: -1;
}
header.masthead.post{
  position: relative;
}


header.masthead .masthead-content {
  z-index: 1;
  position: relative;
}

header.masthead .masthead-content .masthead-heading {
  font-size: 4rem;
}

header.masthead .masthead-content .masthead-subheading {
  font-size: 2rem;
}

header.masthead .bg-circle {
  z-index: 0;
  position: absolute;
  border-radius: 100%;
  background: -webkit-gradient(linear, left bottom, left top, from(#ee0979), to(#ff6a00));
  background: linear-gradient(0deg, #ee0979 0%, #ff6a00 100%);
}

header.masthead .bg-circle-1 {
  height: 90rem;
  width: 90rem;
  bottom: -55rem;
  left: -55rem;
}

header.masthead .bg-circle-2 {
  height: 50rem;
  width: 50rem;
  top: -25rem;
  right: -25rem;
}

header.masthead .bg-circle-3 {
  height: 20rem;
  width: 20rem;
  bottom: -10rem;
  right: 5%;
}

header.masthead .bg-circle-4 {
  height: 30rem;
  width: 30rem;
  top: -5rem;
  right: 35%;
}

@media (min-width: 992px) {
  header.masthead {
    padding-top: calc(10rem + 55px);
    padding-bottom: 10rem;
  }
  header.masthead .masthead-content .masthead-heading {
    font-size: 6rem;
  }
  header.masthead .masthead-content .masthead-subheading {
    font-size: 4rem;
  }
}

#myCarousel{
  width: 100%;
  height: 685px;
  background-color: #5f5f5f;
  z-index: 1;
}

#myCarousel .slide-overlay{
  position: absolute;
  top: 0;
  left: 0;
  height: inherit;
  width: 100%;
  /* background-color: #212529; */
  opacity: 0.5;
   background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 1) 100%); 
}
.bg-primary {
  background-color: #ee0979 !important;
}

.btn-primary {
  background-color: #ee0979;
  border-color: #ee0979;
}

.btn-primary:active, .btn-primary:focus, .btn-primary:hover {
  background-color: #bd0760 !important;
  border-color: #bd0760 !important;
}

.btn-primary:focus {
  -webkit-box-shadow: 0 0 0 0.2rem rgba(238, 9, 121, 0.5);
  box-shadow: 0 0 0 0.2rem rgba(238, 9, 121, 0.5);
}

.btn-secondary {
  background-color: #ff6a00;
  border-color: #ff6a00;
}

.btn-secondary:active, .btn-secondary:focus, .btn-secondary:hover {
  background-color: #cc5500 !important;
  border-color: #cc5500 !important;
}

.btn-secondary:focus {
  -webkit-box-shadow: 0 0 0 0.2rem rgba(255, 106, 0, 0.5);
  box-shadow: 0 0 0 0.2rem rgba(255, 106, 0, 0.5);
}
.spacer{
  width: 100%;
  height: 680px;
  z-index: -2;
}
.background-white { 
  background-color: white;
  position: relative;
  z-index: 1;
}
div.carousel-inner{
  height: inherit;
}
div.carousel-item{
  height: inherit;
}
.container-wide{
  background-color: white;
}
.square-box{
  width: 100%;
  position: relative;
  padding-top: 60%;
}
.square-box-content{
  padding: 2em;
  position: absolute;
  display: flex;
  align-items: center;
  top:0;
  left:0;
  bottom:0;
  right: 0;
}
img.greyscale{
  filter: grayscale(100%);
}
img.greyscale:hover{
  filter: none;
}
span.greyscale{
  color: black;
}
span.greyscale:hover{
  color: #007bff;
}
.your-ad{
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 30px;
}
.my-card {
  position: absolute;
  display: flex;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  align-content: center;
  justify-content: center;
}

h3.section-subheading {
  font-size: 18px;
  font-weight: 400;
  font-style: italic;
  margin-bottom: 55px;
  text-transform: none;
  font-family: 'Droid Serif', serif;
}
.team-member {
  margin-bottom: 50px;
  text-align: center;
}
.section-padding{
  padding: 100px 0px;
}
.team-member img {
  width: 260px;
  height: 260px;
  border: 7px solid #fff;
}
.team-member h4 {
  margin-top: 25px;
  margin-bottom: 0;
  text-transform: none;
}
.team-member p {
  margin-top: 0;
}
ul.social-buttons {
  margin-bottom: 0;
}
ul.social-buttons li a {
  font-size: 20px;
  line-height: 50px;
  display: block;
  width: 50px;
  height: 50px;
  -webkit-transition: all .3s;
  transition: all .3s;
  color: #fff;
  border-radius: 100%;
  outline: 0;
  background-color: #212529;
}
footer .foot-circle{
 color: #f8f9fa;
}
footer .foot-icon{
  color: black;
}
html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}